<template>
	<div class="s-canvas">
		<canvas width="165" height="45" id="canvas" @click="draw"></canvas>
	</div>
</template>
<script>
	export default {
		name: 'SIdentify',
		props: {},
		data() {
			return {
				show_num: []
			}
		},
		methods: {
			draw() {
				var canvas_width = document.getElementById('canvas').clientWidth;
				var canvas_height = document.getElementById('canvas').clientHeight;
				var canvas = document.getElementById("canvas"); //canvas
				var context = canvas.getContext("2d"); //canvas
				canvas.width = canvas_width;
				canvas.height = canvas_height;
				var sCode =
					"A,B,C,E,F,G,H,J,K,L,M,N,P,Q,R,S,T,W,X,Y,Z,1,2,3,4,5,6,7,8,9,0,q,w,e,r,t,y,u,i,o,p,a,s,d,f,g,h,j,k,l,z,x,c,v,b,n,m";
				var aCode = sCode.split(",");
				var aLength = aCode.length; //

				for (var i = 0; i <= 3; i++) {
					var j = Math.floor(Math.random() * aLength); //
					var deg = Math.random() * 30 * Math.PI / 180; //0~30
					var txt = aCode[j]; //
					this.show_num[i] = txt;
					var x = 25 + i * 30; //canvasx
					var y = 22 + Math.random() * 8; //canvasy
					context.font = "bold 30px ";

					context.translate(x, y);
					context.rotate(deg);

					context.fillStyle = this.randomColor();
					context.fillText(txt, 0, 0);

					context.rotate(-deg);
					context.translate(-x, -y);
				}
				for (var i = 0; i <= 5; i++) { //
					context.strokeStyle = this.randomColor();
					context.beginPath();
					context.moveTo(Math.random() * canvas_width, Math.random() * canvas_height);
					context.lineTo(Math.random() * canvas_width, Math.random() * canvas_height);
					context.stroke();
				}
				for (var i = 0; i <= 30; i++) { //
					context.strokeStyle = this.randomColor();
					context.beginPath();
					var x = Math.random() * canvas_width;
					var y = Math.random() * canvas_height;
					context.moveTo(x, y);
					context.lineTo(x + 1, y + 1);
					context.stroke();
				}
				
				this.$emit('Change',this.show_num.join(""))
			},
			randomColor() { //
				var r = Math.floor(Math.random() * 256);
				var g = Math.floor(Math.random() * 256);
				var b = Math.floor(Math.random() * 256);
				return "rgb(" + r + "," + g + "," + b + ")";
			}
		},
		watch: {},
		mounted() {
			this.draw()
		}
	}
</script>

<style scoped>
	.s-canvas{
		background-color: #95f4fc;
		border-radius: 0 50px 50px 0;
	}
	
	.s-canvas canvas{
		display: block;
		cursor: pointer;
	}
</style>
